const fs = require('fs')
const acorn = require('acorn')

function parseFileToAST(filePath) {
  const code = fs.readFileSync(filePath, 'utf8')
  return acorn.parse(code, {
    ecmaVersion: 'latest', //  ECMAScript 
    locations: true // 
  })
}
/**
 *  AST 
 * @param {object} ast  AST
 * @param {string} code 
 * @returns {Array<object>} 
 */
function extractAllFunctions(ast, code) {
  const functions = []
  traverse(ast, code, functions) // 
  return functions
}

/**
 *  AST 
 * @param {object} node  AST 
 * @param {string} code 
 * @param {Array<object>} functions 
 */
function traverse(node, code, functions) {
  // 
  if (node.type === 'FunctionDeclaration') {
    functions.push({
      type: 'FunctionDeclaration',
      name: node.id ? node.id.name : 'anonymous', //  'anonymous'
      content: code.slice(node.start, node.end) // 
    })
  }

  // 
  if (node.type === 'FunctionExpression') {
    functions.push({
      type: 'FunctionExpression',
      name: node.id ? node.id.name : 'anonymous', //  'anonymous'
      content: code.slice(node.start, node.end) // 
    })
  }

  // 
  if (node.type === 'ArrowFunctionExpression') {
    functions.push({
      type: 'ArrowFunctionExpression',
      name: 'anonymous', // 
      content: code.slice(node.start, node.end) // 
    })
  }

  // 
  for (const key in node) {
    if (node[key] && typeof node[key] === 'object') {
      traverse(node[key], code, functions) // 
    }
  }
}

module.exports = {
  parseFileToAST,
  extractAllFunctions,
  traverse
}
